@import '../../assets/scss/variable';
.search{
    $height:44px;
    $buttonWidth:92px;
    $radius:22px;
    display: inline-block;
    width: 312px;
    background: #FFF;
    border-radius: $radius;
    color: #888888;
    position: relative;
    padding-right: $buttonWidth;
    input{
        display: block;
        border: none;
        height:  $height;
        line-height:  $height;
        width: 100%;
        padding: 0 20px;
        outline: none;
        border-top-left-radius: $radius;
        border-bottom-left-radius: $radius;
        font-size: 1.6rem;
    }
    button{
        display: block;
        width: $buttonWidth;
        height: $height;
        line-height: $height;
        background: $theme url(../img/frame/search_icon.png) 10px center no-repeat;
        border-top-right-radius: $radius;
        border-bottom-right-radius: $radius;
        border: none;
        outline: none;
        position: absolute;
        right: 0;
        top: 0;
        font-size: 1.8rem;
        color: #fff;
        padding:0 10px 0 20px;
        cursor: pointer;
    }
}
